@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
}
@media screen and (prefers-reduced-motion: reduce) {
  html html {
    scroll-behavior: auto;
  }
}
@media (max-width: 1023px) {
  html {
    font-size: 14px;
    line-height: 17px;
  }
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .intro,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.intro {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 2fr;
  row-gap: 50px;
  width: 100%;
  min-height: 650px;
  background-color: #101a56;
  color: #f9fbfd;
  overflow: hidden;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
}
@media (max-width: 1023px) {
  .intro {
    min-height: 638px;
    filter: none;
  }
}
@media (max-width: 767px) {
  .intro {
    min-height: 500px;
    row-gap: 14.5px;
  }
}
.intro img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: #101a56;
}
@media (max-width: 767px) {
  .intro img {
    height: 113%;
    object-position: 44% 80%;
  }
}

.intro__image-wrapper {
  position: relative;
  grid-column: 1/2;
  grid-row: 1/3;
  width: 100%;
  height: 100%;
}

.intro__header {
  grid-column: 1/2;
  grid-row: 1/2;
}

.intro__container {
  z-index: 2;
  grid-column: 1/2;
  grid-row: 2/3;
  padding: 0 135px;
  padding-top: 20px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
}
@media (max-width: 1023px) {
  .intro__container {
    padding-top: 22px;
    filter: none;
  }
}
@media (max-width: 767px) {
  .intro__container {
    padding-top: 0;
  }
}
.intro__container h1 {
  margin: 0;
  margin-bottom: 12px;
  padding: 0;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
}
@media (max-width: 1023px) {
  .intro__container h1 {
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .intro__container h1 {
    font-size: 36px;
    line-height: 45px;
  }
}
.intro__container p {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .intro__container p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .intro__container p {
    max-width: 72%;
    font-size: 16px;
  }
}

.header {
  width: 100%;
  padding-top: 22px;
  color: #f9fbfd;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
}
@media (max-width: 1023px) {
  .header {
    padding-top: 12px;
    filter: none;
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 10px;
    min-height: 43px;
  }
  .header.is-open::before {
    content: "";
    position: absolute;
    z-index: 3;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 43, 102, 0.5);
  }
}

.header__nav {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  overflow: hidden;
}
@media (max-width: 767px) {
  .is-open .header__nav {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    display: block;
    padding-top: 10px;
    padding-left: 15px;
    min-width: 283px;
    width: 88%;
    height: 100%;
    background-color: #f9fbfd;
    color: #011c40;
  }
}

.header__logo {
  display: inline-block;
  position: relative;
  z-index: 2;
  padding-top: 13px;
}
@media (max-width: 1023px) {
  .header__logo {
    padding-top: 18px;
    transform: translateX(-2px);
  }
}
@media (max-width: 767px) {
  .header__logo {
    padding-top: 10px;
    transform: translateX(0);
  }
}
.header__logo img {
  position: static;
  width: 70px;
  height: 38px;
  background-color: transparent;
}
@media (max-width: 1023px) {
  .header__logo img {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 1023px) {
  .header__logo img {
    width: 42px;
    height: 22px;
  }
}
@media (hover: hover) {
  .header__logo:hover:not(.focus-visible) {
    filter: drop-shadow(0 0 3px #f9fbfd);
  }
}
.header__logo.focus-visible:focus {
  filter: drop-shadow(0 0 3px #f9fbfd);
}
.is-open .header__logo {
  filter: brightness(0.15) saturate(100) hue-rotate(10deg);
}
@media (hover: hover) {
  .is-open .header__logo:hover:not(.focus-visible) {
    filter: brightness(0.15) saturate(100) hue-rotate(10deg) drop-shadow(0 0 2px #011c40);
  }
}
.is-open .header__logo.focus-visible:focus {
  filter: brightness(0.15) saturate(100) hue-rotate(10deg) drop-shadow(0 0 2px #011c40);
}

.header__nav-list {
  max-width: 80%;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  list-style-type: none;
  transition: 0.3s ease;
  transition-property: transform;
}
@media (max-width: 767px) {
  .header__nav-list {
    display: none;
    transform: translateX(200px);
    pointer-events: none;
  }
  .is-open .header__nav-list {
    display: block;
    visibility: visible;
    max-width: 100%;
    width: 100%;
    max-height: 100vh;
    padding: 17px 15px 20px 10px;
    pointer-events: all;
    transform: translateX(0);
    overflow-y: scroll;
    scrollbar-width: thin;
  }
}

.header__nav-item {
  margin: 0;
  margin-left: 33.5px;
  padding: 0;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
}
@media (max-width: 1023px) {
  .header__nav-item {
    margin-left: 23.7px;
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .is-open .header__nav-item {
    margin-bottom: 14px;
    margin-left: 0;
    font-size: 18px;
    line-height: 22px;
  }
  .is-open .header__nav-item a::after {
    background-color: #011c40;
  }
}
.header__nav-item a {
  position: relative;
  display: inline-block;
  padding: 7px 0;
  color: inherit;
}
.header__nav-item a::after {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #f9fbfd;
}
@media (hover: hover) {
  .header__nav-item a:hover:not(.focus-visible)::after {
    content: "";
    display: block;
  }
}
.header__nav-item a.focus-visible:focus::after {
  content: "";
  display: block;
}
.header__nav-item a:active {
  opacity: 0.5;
}

.header__menu {
  display: none;
}
@media (max-width: 767px) {
  .header__menu {
    position: absolute;
    top: 11px;
    right: 7px;
    box-sizing: border-box;
    display: block;
    width: 40px;
    height: 40px;
    box-shadow: none;
    border: none;
    background-color: rgba(255, 255, 255, 0);
    cursor: pointer;
  }
  .header__menu::before, .header__menu::after {
    content: "";
    position: absolute;
    left: 8px;
    display: block;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    transition: 0.3s ease;
    transition-property: transform;
  }
  .header__menu::before {
    top: 13px;
    box-shadow: 0 6px 0 0 #f9fbfd;
  }
  .header__menu::after {
    top: 25px;
  }
}
@media (max-width: 767px) and (hover: hover) {
  .header__menu:hover:not(.focus-visible) {
    background-color: rgba(249, 251, 253, 0.15);
    filter: drop-shadow(0 0 5px #f9fbfd);
  }
}
@media (max-width: 767px) {
  .header__menu.focus-visible:focus {
    background-color: rgba(249, 251, 253, 0.15);
    filter: drop-shadow(0 0 5px #f9fbfd);
  }
}
@media (max-width: 767px) {
  .header__menu:active {
    opacity: 0.5;
  }
}
@media (max-width: 767px) {
  .is-open .header__menu {
    right: 1px;
  }
  .is-open .header__menu::after, .is-open .header__menu::before {
    top: 19px;
    left: 11px;
    width: 19px;
    box-shadow: none;
    background-color: #011c40;
  }
  .is-open .header__menu::before {
    transform: rotate(45deg);
  }
  .is-open .header__menu::after {
    transform: rotate(-45deg);
  }
}
@media (max-width: 767px) and (hover: hover) {
  .is-open .header__menu:hover:not(.focus-visible) {
    background-color: rgba(1, 28, 64, 0.15);
  }
}
@media (max-width: 767px) {
  .is-open .header__menu.focus-visible:focus {
    background-color: rgba(1, 28, 64, 0.15);
  }
}

.travel {
  padding-top: 120px;
  padding-bottom: 145px;
}
@media (max-width: 1023px) {
  .travel {
    padding-top: 90px;
    padding-bottom: 82px;
  }
}
@media (max-width: 767px) {
  .travel {
    padding-top: 55px;
    padding-bottom: 85px;
  }
}

.travel__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
}
@media (max-width: 1023px) {
  .travel__container {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .travel__container {
    grid-template-columns: 1fr;
    gap: 32px;
  }
}

.travel__description {
  max-width: 515px;
  padding-top: 45px;
  align-self: stretch;
}
@media (max-width: 1023px) {
  .travel__description {
    max-width: 100%;
    padding-top: 0;
  }
}
.travel__description h2 {
  margin: 0;
  margin-top: 10px;
  margin-bottom: 25px;
  padding: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
}
@media (max-width: 1023px) {
  .travel__description h2 {
    max-width: 210px;
    margin-top: 0;
    margin-bottom: 18px;
    font-size: 26px;
    line-height: 36px;
  }
}
@media (max-width: 767px) {
  .travel__description h2 {
    font-size: 24px;
    line-height: 34px;
    margin-bottom: 17px;
  }
}
.travel__description p {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
}
@media (max-width: 1023px) {
  .travel__description p {
    font-size: 16px;
    line-height: 150%;
  }
}

.travel__image-wrapper {
  width: 100%;
  align-self: stretch;
}
@media (max-width: 1023px) {
  .travel__image-wrapper {
    padding-top: 10px;
  }
}
@media (max-width: 767px) {
  .travel__image-wrapper {
    padding-top: 0;
  }
}
.travel__image-wrapper img {
  width: 100%;
  object-fit: contain;
  background-color: #d5eaff;
}

.footer {
  padding-top: 47px;
  padding-bottom: 45px;
  background-color: #011c40;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .footer {
    padding-top: 45px;
  }
}
@media (max-width: 767px) {
  .footer {
    padding-top: 36.5px;
    padding-bottom: 35px;
  }
}

