.contacts__map-image.no-js {
  display: block;
}
.contacts__map-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contacts__text {
  place-self: start end;
  max-width: 500px;
  padding-top: 75px;
}
@media (max-width: 1023px) {
  .contacts__text {
    padding-top: 65px;
  }
}
@media (max-width: 767px) {
  .contacts__text {
    padding-top: 0;
  }
}
.contacts__text p {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
@media (max-width: 1023px) {
  .contacts__text p {
    margin-bottom: 28px;
  }
}

.contacts__wrapper {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  margin-bottom: 22px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .contacts__wrapper {
    align-items: flex-start;
  }
  .contacts__wrapper:first-of-type svg {
    transform: translateY(4px);
  }
}
@media (max-width: 767px) {
  .contacts__wrapper {
    margin-bottom: 20px;
  }
}
.contacts__wrapper svg {
  flex-shrink: 0;
  margin-right: 10px;
}
.contacts__wrapper address {
  font-style: normal;
}
@media (max-width: 1023px) {
  .contacts__wrapper address {
    max-width: 260px;
  }
}
.contacts__wrapper a {
  display: inline-block;
}
@media (hover: hover) {
  .contacts__wrapper a:hover:not(.focus-visible) {
    background-color: rgba(1, 28, 64, 0.1);
  }
}
.contacts__wrapper a.focus-visible:focus {
  background-color: rgba(1, 28, 64, 0.1);
}
.contacts__wrapper a:active {
  opacity: 0.5;
}

